.heading-hover {
	position: relative;
	display: inline-block;
}

.heading-hover::after {
	content: "";
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	background-color: rgb(33, 60, 236);
	transform: scaleX(0);
	transform-origin: left;
	transition: transform 1s;
}

.heading-hover:hover::after {
	transform: scaleX(1);
}


